import { Navbar, Nav, NavDropdown, Container } from "react-bootstrap";
import { Component, React } from "react";

/* 導覽功能表 */
export class NavbarMenu extends Component {
  render() {
    return (
      <Container>
        <Navbar collapseOnSelect expand="sl" bg="light" variant="light">
          <Navbar.Brand href="#home">Universal Astrology 占星網</Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav>
              <NavDropdown.Divider />
              <Nav.Link href="#/index">系統首頁</Nav.Link>
              <Nav.Link href="#/login">登入系統</Nav.Link>
              <Nav.Link href="#/planetInfo">行星資訊</Nav.Link>
              <Nav.Link href="#/asteroidInfo">小行星資訊</Nav.Link>
              <NavDropdown.Divider />
              <Nav.Link href="#/about">關於作者</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </Container>
    );
  }
}
